import { Component, ElementRef, OnInit, Renderer2, ViewChild } from '@angular/core';
import { faCameraRetro } from '@fortawesome/free-solid-svg-icons';
import { AppService } from './app.service';
import { HttpClient, HttpEventType } from '@angular/common/http';
import {ɵBROWSER_SANITIZATION_PROVIDERS, DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  providers: [ɵBROWSER_SANITIZATION_PROVIDERS],
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  @ViewChild('video', { static: true }) videoElement: ElementRef;
  @ViewChild('canvas', { static: true }) canvas: ElementRef;
  title = 'hackathonClient';
  filmIcon = faCameraRetro;
  public imagePath;
  imgURL: any;
  imageData:any;
  newImgURL: any;
  public message: string;
  fileData: File = null;
  previewUrl:any = null;
  fileUploadProgress: string = null;
  uploadedFilePath: string = null;
  responseData: any;
  videoWidth = 0;
  videoHeight = 0;
  cameraOn : boolean = false;

  constraints = {
    video: {
      facingMode: "environment",
      width: { ideal: 2000 },
      height: { ideal: 2000 }
    }
  };
  constructor(private appService: AppService,
              private http: HttpClient,
              private sanitizer: DomSanitizer,
              private renderer: Renderer2) { }

  ngOnInit() {
    this.cameraOn=true;
    this.startCamera();
  }
  preview() {
    // Show preview 
    var mimeType = this.fileData.type;
    if (mimeType.match(/image\/*/) == null) {
      return;
    }
 
    var reader = new FileReader();      
    reader.readAsDataURL(this.fileData); 
    reader.onload = (_event) => { 
      this.previewUrl = reader.result; 
    }
  }

  startCamera() {
    if (!!(navigator.mediaDevices && navigator.mediaDevices.getUserMedia)) {
      navigator.mediaDevices.getUserMedia(this.constraints).then(this.attachVideo.bind(this)).catch(this.handleError);
    } else {
      alert('Sorry, camera not available.');
    }
  }

  fileProgress(fileInput: any) {
    this.fileData = <File>fileInput.target.files[0];
    this.fileUploadProgress = this.fileData.name;
    this.preview();
    this.showImage(this.fileData);
    }

  showImage(files) {
    if (files.length === 0)
      return;

    var mimeType = files.type;
    if (mimeType.match(/image\/*/) == null) {
      this.message = "Only images are supported.";
      return;
    }

    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files); 
    reader.onload = (_event) => { 
      this.imgURL = reader.result; 
    }
  }
  processImage() {
    console.log('Forwarding Request');
    console.log(this.fileData);
    const formData = new FormData();
    formData.append('image', this.fileData);
    this.http.post('/api/mask_detector',formData).subscribe(
      (val: any) => {
        this.responseData = val;
      },
      response => {
        console.log('error', response);
      },
      () => {
        console.log(this.responseData);
        this.newImgURL = this.responseData.image;  
        this.imageData = 'data:image/png;base64,' + this.responseData.image;
        this.newImgURL = this.sanitizer.bypassSecurityTrustUrl(this.imageData);
        //  this.sanitzedImageData = sanitizer.bypassSecurityTrustUrl(imageData); 
        // const reader = new FileReader();
        // reader.readAsDataURL(this.responseData.image);
        // reader.onload = (_event) => { 
        //   this.newImgURL = reader.result; 
        // }
        
        //console.log(this.newImgURL);
        console.log('completed.');
      });
  }

  capture() {
    this.renderer.setProperty(this.canvas.nativeElement, 'width', this.videoWidth);
    this.renderer.setProperty(this.canvas.nativeElement, 'height', this.videoHeight);
    this.canvas.nativeElement.getContext('2d').drawImage(this.videoElement.nativeElement, 0, 0);
    this.stopCamera();
  }
  stopCamera(){
    console.log('stopping');
    this.cameraOn = false;
  }
  
  attachVideo(stream) {
    
    this.renderer.setProperty(this.videoElement.nativeElement, 'srcObject', stream);
    this.renderer.listen(this.videoElement.nativeElement, 'play', (event) => {
      this.videoHeight = this.videoElement.nativeElement.videoHeight;
      this.videoWidth = this.videoElement.nativeElement.videoWidth;
    });
  }
  handleError(error) {
    console.log('Error: ', error);
  }
}
